export default class WifiManager {
  constructor() {
    this.select_index = 0;
    this.wifi_list = [];
    this.wireless = null;
    if (process.env.NODE_ENV === "production") {
      this.wifi = window["wifi"];
      this.init();
    }
  }
  init() {
    this.wifi.init({
      iface: null // network interface, choose a random wifi interface if set to null
    });
  }

  get_wifi_list() {
    return new Promise((resolve, reject) => {
      this.wifi.scan((err, networks) => {
        if (err) {
          return resolve([]);
        } else {
          console.log(JSON.stringify(networks, null, 4));
          return resolve(networks);
        }
      });
    });
  }
  connect(name, pwd) {
    return new Promise((resolve, reject) => {
      this.wifi.connect({ ssid: name, password: pwd }, err => {
        try {
          if (err) {
            console.error(err);
            return resolve(false);
          }
          console.log("Connected");
          return resolve(true);
        } catch (error) {
          return resolve(false);
        }
      });
    });
  }
  disconnect(name, pwd) {
    return new Promise((resolve, reject) => {
      try {
        this.wifi.disconnect(err => {
          if (err) {
            console.error(err);
            return resolve(false);
          }
          console.log("Connected");
          return resolve(true);
        });
      } catch (error) {
        return resolve(false);
      }
    });
  }
}



